.crumbs{
  display: flex;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 7px;
}

.crumbs__link{
  font-size: 16px;
  line-height: 24px;
  margin-right: 20px;
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  img{
    margin-right: 10px;
    width: 16px;
    transform: translateY(-3px);
  }
  &.--home{
    color: #45B0DF;
    &:after{
      display: block;
      content: " >";
      color: #252733;
      margin-left: 10px;
    }
  }
}
