// Common styles of the project
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700,900&display=swap');
html, body {
    height: 100%;
    font-family: "Montserrat";
    font-size: 14px;
    line-height: 18px;
    color: #252733;
    background-color: #F0F1F3;
}

.title{
    margin: 0;
    margin-bottom: 44px;
    font-weight: 300;
    line-height: 30px;
}
.page__wrapper {
    min-height: 100%;
    margin-bottom: -50px;
}
*{
    box-sizing: border-box;
}
* html .page__wrapper {
    height: 100%;
}

.page__buffer {
    height: 50px;
}

p{
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 0px;
}

@import 'entry/partials/_template/_template.scss';
