.create__container{
  width: calc(100% - 70px);
  margin: 0 auto;
}
.create{
  background-color: #fff;
  border-radius: 8px;
  height: calc(100vh - 56px);
  position: relative;
  &:after{
    content: "";
    position: absolute;
    right: 340px;
    width: 245px;
    height: 250px;
    top: 408px;
    background: url(../img/minified-svg/bot.svg) no-repeat center / contain;
  }
  &.--initiate{
    height: calc(100vh - 90px);
    .create__body{
      &:after{
        content: "";
        position: absolute;
        top: 50%;
        right: 28px;
        transform: translateY(-50%);
        width: 477px;
        height: 627px;
        background: url(../img/minified-svg/phone.svg) no-repeat center / contain;
      }
      &:before{
        content: "";
        position: absolute;
        right: 11px;
        top: 12px;
        width: 290px;
        height: calc(100% - 24px);
        background-color: #56CCF2;
        opacity: .2;
      }
    }
  }
}
.create__head{
  border-bottom: 1px solid #E0E0E0;
}

.create__body{
  padding: 0 31px;
  position: relative;
  position: absolute;
  left: 0;
  top: 48px;
  width: 100%;
  padding-top: 32px;
  height: calc(100% - 48px);
}

.initiate__title{
  font-size: 18px;
  font-weight: 500;
  display: flex;
  align-items: flex-end;
}

.initiate__ico{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  background-color: #DDF4FE;
  border-radius: 12px;
  margin-right: 9px;
}


.initiate__text{
  max-width: 500px;
  margin-bottom: 38px;
}

.initiate__label{
  line-height: 22px;
  p{
    margin-bottom: 4px;
  }
}


.initiate__input{
  max-width: 400px;
  width: 100%;
  border-radius: 3px;
  padding-left: 17px;
  border: 1px solid #CEDCE4;
  height: 44px;
  &:focus{
    border-color: #32BBF7;
  }
}

.initiate__bottom{
  position: absolute;
  left: 32px;
  bottom: 61px;
}


.btn.--blue{
  padding-left: 32px;
  padding-right: 32px;
  min-width: 156px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  text-transform: uppercase;
  font-size: 16px;
  color: #fff;
  background: linear-gradient(216.64deg, #5ACDFF 16.74%, #32BBF7 75.99%);
  border-radius: 8px;
  display: inline-block;
  text-decoration: none;
  font-weight: 500;
}