html, body {
  height: 100%;
  font-family: "Montserrat";
  font-size: 14px;
  line-height: 18px;
  color: #252733;
  background-color: #F0F1F3;
}

.title {
  margin: 0;
  margin-bottom: 44px;
  font-weight: 300;
  line-height: 30px;
}

.page__wrapper {
  min-height: 100%;
  margin-bottom: -50px;
}

* {
  box-sizing: border-box;
}

* html .page__wrapper {
  height: 100%;
}

.page__buffer {
  height: 50px;
}

p {
  font-size: 14px;
  line-height: 18px;
  margin-bottom: 0px;
}

.crumbs {
  display: flex;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 7px;
}

.crumbs__link {
  font-size: 16px;
  line-height: 24px;
  margin-right: 20px;
  display: inline-flex;
  align-items: center;
  text-decoration: none;
}

.crumbs__link img {
  margin-right: 10px;
  width: 16px;
  transform: translateY(-3px);
}

.crumbs__link.--home {
  color: #45B0DF;
}

.crumbs__link.--home:after {
  display: block;
  content: " >";
  color: #252733;
  margin-left: 10px;
}

.tabs {
  display: flex;
  justify-content: center;
}

.tab {
  text-transform: uppercase;
  height: 48px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding-right: 20px;
  width: 210px;
  background: url(../img/minified-svg/tab-arrow.svg) no-repeat right center/auto 42px;
  cursor: pointer;
  position: relative;
}

.tab.active:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: calc(100% - 18px);
  height: 4px;
  background-color: #56CCF2;
  border-radius: 6px;
}

.tab img {
  margin-right: 13px;
}

.create__container {
  width: calc(100% - 70px);
  margin: 0 auto;
}

.create {
  background-color: #fff;
  border-radius: 8px;
  height: calc(100vh - 56px);
  position: relative;
}

.create:after {
  content: "";
  position: absolute;
  right: 340px;
  width: 245px;
  height: 250px;
  top: 408px;
  background: url(../img/minified-svg/bot.svg) no-repeat center/contain;
}

.create.--initiate {
  height: calc(100vh - 90px);
}

.create.--initiate .create__body:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 28px;
  transform: translateY(-50%);
  width: 477px;
  height: 627px;
  background: url(../img/minified-svg/phone.svg) no-repeat center/contain;
}

.create.--initiate .create__body:before {
  content: "";
  position: absolute;
  right: 11px;
  top: 12px;
  width: 290px;
  height: calc(100% - 24px);
  background-color: #56CCF2;
  opacity: .2;
}

.create__head {
  border-bottom: 1px solid #E0E0E0;
}

.create__body {
  padding: 0 31px;
  position: relative;
  position: absolute;
  left: 0;
  top: 48px;
  width: 100%;
  padding-top: 32px;
  height: calc(100% - 48px);
}

.initiate__title {
  font-size: 18px;
  font-weight: 500;
  display: flex;
  align-items: flex-end;
}

.initiate__ico {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  background-color: #DDF4FE;
  border-radius: 12px;
  margin-right: 9px;
}

.initiate__text {
  max-width: 500px;
  margin-bottom: 38px;
}

.initiate__label {
  line-height: 22px;
}

.initiate__label p {
  margin-bottom: 4px;
}

.initiate__input {
  max-width: 400px;
  width: 100%;
  border-radius: 3px;
  padding-left: 17px;
  border: 1px solid #CEDCE4;
  height: 44px;
}

.initiate__input:focus {
  border-color: #32BBF7;
}

.initiate__bottom {
  position: absolute;
  left: 32px;
  bottom: 61px;
}

.btn.--blue {
  padding-left: 32px;
  padding-right: 32px;
  min-width: 156px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  text-transform: uppercase;
  font-size: 16px;
  color: #fff;
  background: linear-gradient(216.64deg, #5ACDFF 16.74%, #32BBF7 75.99%);
  border-radius: 8px;
  display: inline-block;
  text-decoration: none;
  font-weight: 500;
}
