.tabs{
  display: flex;
  justify-content: center;
}

.tab{
  text-transform: uppercase;
  height: 48px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding-right: 20px;
  width: 210px;
  background: url(../img/minified-svg/tab-arrow.svg) no-repeat right center / auto 42px;
  cursor: pointer;
  position: relative;
  &.active:after{
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: calc(100% - 18px);
    height: 4px;
    background-color: #56CCF2;
    border-radius: 6px;
  }
  img{
    margin-right: 13px;
  }
}

